import * as THREE from 'three';
import Layer from './Layer';
import Utils from '../Common/Utils';
import GeoJSON from '../Common/GeoJSON';
import { CSS2DObject } from '../jsm/renderers/CSS2DRenderer';
export default class LabelLayer extends Layer {
    constructor(props) {
        super();
        this.style = {
            fontColor: "#ffffff",
            fontSize: 12,
            color: 'rgba(10,18,51,0.8)',
            opacity: 1.0,
            lineColor: '#ffffff',
            lineOpacity: 1.0
        };
        Object.assign(this, props);
        this.init();
    }
    init() {
        this.lineMaterial = new THREE.PointsMaterial({
            color: 0xffffff,
            // sizeAttenuation: false,
            // alphaTest: .5,
            // vertexColors: THREE.VertexColors,
            // color: 0x00ff00,
            // size: 200,
            transparent: true,
            // blending: THREE.AdditiveBlending,
            // depthTest: true,
            // depthWrite: false,
            //map: this.view.textureLoader.load('')
        });
    }
    geoJSON(data) {
        GeoJSON.toGeometry(data, rs => {
            if(rs.type === 'MultiPoint' || rs.type === 'Point') {
                this.createMesh(rs.type === 'Point' ? [rs.coordinates] : rs.coordinates, {
                    height: rs.properties.height !== undefined && rs.properties.height !== null ? rs.properties.height : this.height
                });
            }
        });
    }
    getSpriteTexture(pos) {
        let texture;
        if(true) {
            texture = this.view.textureLoader.load('images/earth/icon/24.png');
        } else {
            let canvas = document.createElement('canvas');
            let ctx = canvas.getContext('2d');
            ctx.fillStyle = '#ffff00';
            ctx.font = 'Bold 100px Arial';
            ctx.lineWidth = 4;
            ctx.fillText('AAAAAAA', 4, 104);
            texture = new THREE.Texture(canvas);
        }
        texture.needsUpdate = true;
        return texture;
    }
    setData(data) {
        this.clear();
        //this.geoJSON(data);
        this.doms = [];
        // const material = new THREE.MeshBasicMaterial({
        //     color: 0x00ff00
        // });
        for(let i = 0; i < data.length; i++) {
            let start = Utils.lonLatToWorld([data[i].lng, data[i].lat]);
            let pos = Utils.lonLatToWorld([data[i].lng, data[i].lat], 200);
            //this.view.textureLoader.load(`/images/earth/icon/swcs_tb${data.length - 1 - i}.png`, texture => {
                // let material = new THREE.SpriteMaterial({
                //     // color: new THREE.Color(0, 0, 0),
                //     transparent: true,
                //     // opacity: 1.0,
                //     // depthWrite: true,
                //     // depthTest: true,
                //     map: texture
                // });
                // let cube = new THREE.Sprite(material);

                let dom = document.createElement('div');
                dom.style = 'padding: 5px;border-radius: 4px;background-color: rgba(10,18,51,.8);';
                dom.innerHTML = `
                    <div style="width: 30px;height: 30px;float: left;">
                        <img src="${ data[i].iconUrl }" style="width: 100%;height: 100%;"/>
                    </div>
                    <div style="overflow: hidden;padding: 4px 8px;">${ data[i].content }</div>
                `;
                let cube = new CSS2DObject(dom);

                // Utils.setDirection(cube, pos, [0, 0, 1]);
                // cube.scale.set(texture.image.width / 4, texture.image.height / 4. 0);
                cube.position.x = pos[0];
                cube.position.y = pos[1];
                cube.position.z = pos[2];

                let geometry = new THREE.BufferGeometry();
                let positions = new Float32Array(6);
                positions[0] = start[0];
                positions[1] = start[1];
                positions[2] = start[2];
                positions[3] = pos[0];
                positions[4] = pos[1];
                positions[5] = pos[2];

                geometry.setAttribute('position', new THREE.BufferAttribute(positions, 3));
                let line = new THREE.Line(geometry, this.lineMaterial);

                this.node.add(line);
                this.node.add(cube);
            //});
            this.doms.push(dom);
        }
        this.setStyle(this.style);
    }
    createMesh(coordinates, config) {
    }
    setStyle(style) {
        let {
            fontColor,
            color,
            opacity,
            fontSize,
            lineColor,
            lineOpacity
        } = style;
        // if(color !== undefined) {
        //     this.material.color.set(color);
        // }
        // if(opacity !== undefined) {
        //     this.material.opacity = opacity;
        // }
        // if(size !== undefined) {
        //     this.material.size = size;
        // }
        for(let i = 0; i < this.doms.length; i++) {
            this.doms[i].style.backgroundColor = color;
            this.doms[i].style.opacity = opacity;
            this.doms[i].style.fontSize = fontSize + 'px';
            this.doms[i].style.color = fontColor;
        }
        if(lineColor !== undefined) {
            this.lineMaterial.color.set(lineColor);
        }
        if(lineOpacity !== undefined) {
            this.lineMaterial.opacity = lineOpacity;
        }
    }
}