const GeoJSON = {
    toGeometry: function(geoJson, callback, outer = false, result = [], properties) {
        let type = geoJson['type'];
        if(type === 'FeatureCollection') {
            for(let i = 0; i < geoJson['features'].length; i++) {
                this.toGeometry(geoJson['features'][i], callback, outer, result, geoJson['features'][i]['properties']);
            }
        } else if(type === 'Feature') {
            this.toGeometry(geoJson['geometry'], callback, outer, result, geoJson['properties']);
        } else if(type === 'GeometryCollection') {
            for(let i = 0; i < geoJson['geometries'].length; i++) {
                this.toGeometry(geoJson['geometries'][i], callback, outer, result, properties);
            }
        } else if(type === 'MultiPolygon') {
            for(let i = 0; i < geoJson['coordinates'].length; i++) {
                this.rewindRings(geoJson['coordinates'][i], outer);
                // let obj = {};
                // obj['type'] = 'Polygon';
                // obj['coordinates'] = geoJson['coordinates'][i];
                // obj['properties'] = properties;
                // result.push(obj);
                // if(callback) {
                //     callback(obj);
                // }
            }
            let obj = {};
            obj['type'] = 'MultiPolygon';
            obj['coordinates'] = geoJson['coordinates'];
            obj['properties'] = properties;
            result.push(obj);
            if(callback) {
                callback(obj);
            }
        } else if(type === 'Polygon') {
            this.rewindRings(geoJson['coordinates'], outer);
            let obj = {};
            obj['type'] = 'Polygon';
            obj['coordinates'] = geoJson['coordinates'];
            obj['properties'] = properties;
            result.push(obj);
            if(callback) {
                callback(obj);
            }
        } else if(type === 'MultiLineString') {
            // for(let i = 0; i < geoJson['coordinates'].length; i++) {
                // let obj = {};
                // obj['type'] = 'LineString';
                // obj['coordinates'] = geoJson['coordinates'][i];
                // obj['properties'] = properties;
                // result.push(obj);
                // if(callback) {
                //     callback(obj);
                // }
            // }
            let obj = {};
            obj['type'] = 'MultiLineString';
            obj['coordinates'] = geoJson['coordinates'];
            obj['properties'] = properties;
            result.push(obj);
            if(callback) {
                callback(obj);
            }
        } else if(type === 'LineString') {
            let obj = {};
            obj['type'] = 'LineString';
            obj['coordinates'] = geoJson['coordinates'];
            obj['properties'] = properties;
            result.push(obj);
            if(callback) {
                callback(obj);
            }
        } else if(type === 'MultiPoint') {
            // for(let i = 0; i < geoJson['coordinates'].length; i++) {
                // let obj = {};
                // obj['type'] = 'Point';
                // obj['coordinates'] = geoJson['coordinates'][i];
                // obj['properties'] = properties;
                // result.push(obj);
                // if(callback) {
                //     callback(obj);
                // }
            // }
            let obj = {};
            obj['type'] = 'MultiPoint';
            obj['coordinates'] = geoJson['coordinates'];
            obj['properties'] = properties;
            result.push(obj);
            if(callback) {
                callback(obj);
            }
        } else if(type === 'Point') {
            let obj = {};
            obj['type'] = 'Point';
            obj['coordinates'] = geoJson['coordinates'];
            obj['properties'] = properties;
            result.push(obj);
            if(callback) {
                callback(obj);
            }
        }
        return result;
    },
    rewindRings: function(rings, outer) {
        if(rings.length <= 0) {
            return;
        }
        this.rewindRing(rings[0], outer); //
        for(let i = 1; i < rings.length; i++) { //
            this.rewindRing(rings[i], outer);
        }
    },
    rewindRing: function(ring, dir) {
        let area = 0;
        let minLng = Infinity;
        let maxLng = -Infinity;
        let minLat = Infinity;
        let maxLat = -Infinity;
        for(let i = 0, len = ring.length, j = len - 1; i < len; j = i++) {
            if(ring[i][0] > maxLng) {
                maxLng = ring[i][0];
            }
            if(ring[i][0] < minLng) {
                minLng = ring[i][0];
            }
            if(ring[i][1] > maxLat) {
                maxLat = ring[i][1];
            }
            if(ring[i][1] < minLat) {
                minLat = ring[i][1];
            }
            area += (ring[i][0] - ring[j][0]) * (ring[i][1] + ring[j][1]);
        }
        if(area >= 0 !== !!dir) {
            ring.reverse();
        }
    }
};
export default GeoJSON;