export default class Viewport {
    constructor(props) {
        const { camera, el } = props;
        this._camera = camera;
        this.el = el;
        //this.name = "0";
        this.init();
    }
    init() {
        let width = this.el.offsetWidth, height = this.el.offsetHeight;
        this.top = this.el.clientTop;
        this.left = this.el.clientLeft;
        this.bottom = this.top + height;
        this.right = this.left + width;
        this.actualHeight = height;
        this.actualWidth = width;
    }
    // isPointInside(point2d) {
    //     return this.left <= point2d[0] && this.right >= point2d[0] && this.top <= point2d[1] && this.bottom >= point2d[1];
    //     /*let vec = new THREE.Vector3(0, 0, -10); //in front of camera
    //     let camera = this._camera;
    //     //check if within camera's view:
    //     camera.updateMatrix(); // make sure camera's local matrix is updated
    //     camera.updateMatrixWorld(); // make sure camera's world matrix is updated
    //     camera.matrixWorldInverse.getInverse(camera.matrixWorld);
    //     let frustum = new THREE.Frustum();
    //     frustum.setFromMatrix(new THREE.Matrix4().multiplyMatrices(camera.projectionMatrix, camera.matrixWorldInverse));
    //       if (frustum.containsPoint(vec)) {
    //         //console.log('within camera view');
    //         return true;
    //     } else {
    //         //console.log('outside camera view');
    //         return false;
    //     }*/
    // }
    isPointInside(pos) {
        return this.left <= pos[0] && this.right >= pos[0] && this.top <= pos[1] && this.bottom >= pos[1];
    }
}