import * as THREE from 'three';
import Layer from './Layer';
import Utils from '../Common/Utils';
import GeoJSON from '../Common/GeoJSON';

export default class BuildingLayer extends Layer {
    constructor(props) {
        super();
        this.effect = false;
        this.topColor = 0x4d7384;
        this.sideColor = 0x66a3ee;
        this.height = 100;
        this.buildingTextureSize = [20, 20];
        this.buildingMaskTextureSize = [60, 60];
        // this.style = {
        //     topColor: '#4d7384',
        //     sideColor: '#66a3ee',
        //     height: 100,
        //     buildingTextureSize: [20, 20],
        //     buildingMaskTextureSize: [60, 60],
        //     effect: false
        // };
        Object.assign(this, props);
        this.init();
    }
    init() {
        let material1 = new THREE.MeshStandardMaterial({//MeshBasicMaterial
            color: this.topColor,
            side: THREE.BackSide,
            //map: null,
            //colorwrite: false,
            transparent: true,
            //depthWrite: true,
            //depthTest: true,
            opacity: 1.0
        });
        //material1.needsUpdate = true;
        let material2 = new THREE.MeshStandardMaterial({
            color: this.sideColor,
            side: THREE.BackSide,
            //map: null,
            //colorwrite: false,
            transparent: true,
            //depthWrite: true,
            //depthTest: true,
            opacity: 1.0
        });
        //material2.needsUpdate = true;
        this.material = [material1, material2];
    }
    geoJSON(data) {
        GeoJSON.toGeometry(data, rs => {
            if(rs.type === 'MultiPolygon' || rs.type === 'Polygon') {
                this.createMesh(rs.type === 'Polygon' ? [rs.coordinates] : rs.coordinates, {
                    height: rs.properties.height !== undefined && rs.properties.height !== null ? rs.properties.height : this.height
                });
            }
        });
    }
    setData(data) {
        this.clear();
        this.geoJSON(data);
        this.setStyle(this.style);
    }
    createMesh(coordinates, config) {
        let { buildingTextureSize, buildingMaskTextureSize, height } = this;
        height = config.height;
        //this.preHeight = height;
        let node = new THREE.Group();
        this.node.add(node);
        const coords = coordinates;
        const center = Utils.getCenterCoord(coords[0][0]);
        const centerPos = Utils.lonLatToWorld(center);
        Utils.setPosition(node, centerPos);
        Utils.setDirection(node, centerPos, [0, 1, 0]);
        for(let i = 0; i < coords.length; i++) {
            let polygon = coords[i];
            let outer = [];
            for(let k = 0; k < polygon.length; k++) {
                if(k === 0) {
                    for(let j = 0; j < polygon[k].length; j++) {
                        let point = Utils.worldToLocal(node, Utils.lonLatToWorld(polygon[k][j]));
                        point = new THREE.Vector3(point[0], point[2], point[1]);
                        outer.push(point);
                    }
                }
            }
            let shape = new THREE.Shape(outer);
            // if (holes) {
            //     for (let n = 0; n < holes.length; n++) {
            //         let holesPoints = []
            //         for (let m = 0; m < holes[n].length / 3; m++) {
            //             holesPoints.push(new THREE.Vector3(holes[n][3 * m], holes[n][3 * m + 2], 0))
            //         }
            //         let holesPaths = new THREE.Path(holesPoints);
            //         shape.holes.push(holesPaths);
            //     }
            // }
            let bufferGeometry = new THREE.BufferGeometry();
            bufferGeometry.topUV = [];
            bufferGeometry.sideUV1 = [];
            bufferGeometry.sideUV2 = [];
            bufferGeometry.sideUV3 = [];
            bufferGeometry.sideUV4 = [];
            bufferGeometry.sideUV5 = [];
            bufferGeometry.sideUV6 = [];
            bufferGeometry.sideUV7 = [];
    
            if(bufferGeometry.boundingBox === null) {
                bufferGeometry.boundingBox = new THREE.Box3();
            }
            bufferGeometry.boundingBox.setFromPoints(outer);
            let UVGenerator = {
                generateTopUV: (geometry, indexA, indexB, indexC, indexD) => {
                    let c = (indexA[3 * indexB] - bufferGeometry.boundingBox.min.x) / (bufferGeometry.boundingBox.max.x - bufferGeometry.boundingBox.min.x),
                        h = 1 - (indexA[3 * indexB + 1] - bufferGeometry.boundingBox.min.y) / (bufferGeometry.boundingBox.max.y - bufferGeometry.boundingBox.min.y),
                        f = (indexA[3 * indexC] - bufferGeometry.boundingBox.min.x) / (bufferGeometry.boundingBox.max.x - bufferGeometry.boundingBox.min.x),
                        d = 1 - (indexA[3 * indexC + 1] - bufferGeometry.boundingBox.min.y) / (bufferGeometry.boundingBox.max.y - bufferGeometry.boundingBox.min.y),
                        p = (indexA[3 * indexD] - bufferGeometry.boundingBox.min.x) / (bufferGeometry.boundingBox.max.x - bufferGeometry.boundingBox.min.x),
                        m = 1 - (indexA[3 * indexD + 1] - bufferGeometry.boundingBox.min.y) / (bufferGeometry.boundingBox.max.y - bufferGeometry.boundingBox.min.y),
                        g = [new THREE.Vector2(c, h), new THREE.Vector2(f, d), new THREE.Vector2(p, m)];
                    bufferGeometry.topUV.push.call(bufferGeometry.topUV, g[0].x, g[0].y, g[1].x, g[1].y, g[2].x, g[2].y);
                    return g;
                },
                generateSideWallUV: (geometry, indexA, indexB, indexC) => {
                    let l = [indexA[3 * indexB], indexA[3 * indexB + 1]], 
                        p = [indexA[3 * indexC], indexA[3 * indexC + 1]], 
                        m = Utils.getDistanceFromArray(l, p) / buildingTextureSize[0], 
                        g = height / buildingTextureSize[1], 
                        v = Utils.getDistanceFromArray(l, p) / buildingMaskTextureSize[0], 
                        y = height / buildingMaskTextureSize[1],
                        toIn = function(x, y, sideUV) {
                            let n = [new THREE.Vector2(0, 0), new THREE.Vector2(x, 0), new THREE.Vector2(x, y), new THREE.Vector2(0, y)];
                            sideUV.push.call(sideUV, n[0].x, n[0].y, n[1].x, n[1].y, n[3].x, n[3].y);
                            sideUV.push.call(sideUV, n[1].x, n[1].y, n[2].x, n[2].y, n[3].x, n[3].y);
                        };
                    let x = [new THREE.Vector2(0, 0), new THREE.Vector2(1, 0), new THREE.Vector2(1, 1), new THREE.Vector2(0, 1)];
                    toIn(-1, 1, bufferGeometry.sideUV1);
                    toIn(-1, g, bufferGeometry.sideUV2);
                    toIn(-m, 1, bufferGeometry.sideUV3);
                    toIn(-m, g, bufferGeometry.sideUV4);
                    toIn(-1, y, bufferGeometry.sideUV5);
                    toIn(-v, 1, bufferGeometry.sideUV6);
                    toIn(-v, y, bufferGeometry.sideUV7);
                    return x;
                }
            };
            let extrudeBufferGeometry = new THREE.ExtrudeBufferGeometry(shape, {
                steps: 1,
                depth: height,
                material: 0,
                extrudeMaterial: 1,
                bevelEnabled: false,
                UVGenerator
            });
            let extrudeNormals = extrudeBufferGeometry.attributes.normal.array,
                extrudePositions = extrudeBufferGeometry.attributes.position.array;
            for(let h = 0; h < extrudePositions.length; h += 3) {
                let a = extrudePositions[h + 1];
                extrudePositions[h + 1] = extrudePositions[h + 2];
                extrudePositions[h + 2] = a;
                extrudeNormals[h] = -extrudeNormals[h];
                extrudeNormals[h + 1] = -extrudeNormals[h + 1];
                extrudeNormals[h + 2] = -extrudeNormals[h + 2];
                let b = extrudeNormals[h + 1];
                extrudeNormals[h + 1] = extrudeNormals[h + 2];
                extrudeNormals[h + 2] = b;
            }    
            extrudeBufferGeometry.topUV = bufferGeometry.topUV;
            extrudeBufferGeometry.sideUV1 = bufferGeometry.sideUV1;
            extrudeBufferGeometry.sideUV2 = bufferGeometry.sideUV2;
            extrudeBufferGeometry.sideUV3 = bufferGeometry.sideUV3;
            extrudeBufferGeometry.sideUV4 = bufferGeometry.sideUV4;
            extrudeBufferGeometry.sideUV5 = bufferGeometry.sideUV5;
            extrudeBufferGeometry.sideUV6 = bufferGeometry.sideUV6;
            extrudeBufferGeometry.sideUV7 = bufferGeometry.sideUV7;
            //extrudeBufferGeometry.setAttribute("uv", new THREE.BufferAttribute(Utils.arrayToFloat32Array(bufferGeometry.topUV.concat(bufferGeometry.sideUV2)), 2));
            extrudeBufferGeometry.setAttribute("uv2", new THREE.BufferAttribute(Utils.arrayToFloat32Array(bufferGeometry.topUV.concat(bufferGeometry.sideUV1)), 2));
            //extrudeBufferGeometry.setAttribute("uv3", new THREE.BufferAttribute(Utils.arrayToFloat32Array(bufferGeometry.topUV.concat(bufferGeometry.sideUV4)), 2));
            extrudeBufferGeometry.attributes.uv.needsUpdate = true;
            let obj = new THREE.Mesh(extrudeBufferGeometry, this.material);
            node.add(obj);
            // this.updateEffect(obj, {
            //     name: 'outline',
            //     value: 1
            // }, true);

            //console.log('extrudeBufferGeometry', extrudeBufferGeometry);
        }
    }
    setStyle(style) {
        let {
            topImg,
            topColor,
            topOpacity,
            sideImg,
            sideColor,
            sideOpacity,
            height,
            effect
        } = style;

        if(topImg) {
            this.material[0].map = this.view.textureLoader.load(topImg, item => {
                item.wrapS = THREE.RepeatWrapping;
                item.wrapT = THREE.RepeatWrapping;
                item.anisotropy = 16;
                item.needsUpdate = true;
                return item;
            });
        } else {
            this.material[0].map = this.view.textureLoader.load('images/s.png', item => {
                item.wrapS = THREE.RepeatWrapping;
                item.wrapT = THREE.RepeatWrapping;
                item.anisotropy = 16;
                item.needsUpdate = true;
                return item;
            });
        }

        if(topColor !== undefined) {
            this.material[0].color.set(topColor);
        }
        if(topOpacity !== undefined) {
            this.material[0].opacity = topOpacity;
        }

        if(sideImg) {
            this.material[1].map = this.view.textureLoader.load(sideImg, item => {
                item.wrapS = THREE.RepeatWrapping;
                item.wrapT = THREE.RepeatWrapping;
                item.anisotropy = 16;
                item.needsUpdate = true;
                return item;
            });
        } else {
            this.material[1].map = this.view.textureLoader.load('images/s.png', item => {
                item.wrapS = THREE.RepeatWrapping;
                item.wrapT = THREE.RepeatWrapping;
                item.anisotropy = 16;
                item.needsUpdate = true;
                return item;
            });
        }

        if(sideColor !== undefined) {
            this.material[1].color.set(sideColor);
        }
        if(sideOpacity !== undefined) {
            this.material[1].opacity = sideOpacity;
        }
        if(height !== undefined) {
            if(this.preHeight === undefined || this.preHeight === null) {
                this.preHeight = height;
            }
            let scale = height / this.preHeight;
            this.node.children.forEach(item => {
                item.scale.y = scale || 1;
            });
            //this.node.scale.y = scale;
        }
        // if(effect !== undefined) {
        //     for(let i = 0; i < this.node.children.length; i++) {
        //         for(let j = 0; j < this.node.children[i].length; j++) {
        //             this.updateEffect(this.node.children[i][j], {
        //                 //name: 'outline',
        //                 name: 'urealBloom',
        //                 value: 1
        //             }, effect);
        //         }
        //     }
        // }
        this.setEffect(effect);
    }
    setEffect(isEffect) {
        for(let i = 0; i < this.node.children.length; i++) {
            for(let j = 0; j < this.node.children[i].children.length; j++) {
                this.updateEffect(this.node.children[i].children[j], {
                    //name: 'outline',
                    name: 'urealBloom',
                    value: 0.3
                }, isEffect);
            }
        }
    }
    // updateColor(color) {
    //     for(let i = 0; i < this.material.length; i++) {
    //         this.material[i].color.setHex(color);
    //     }
    // }
    // updateOpacity(opacity) {
    //     for(let i = 0; i < this.material.length; i++) {
    //         this.material[i].opacity = opacity;
    //     }
    // }
    // updateHeight(height) {
    //     const scale = height / this.height;
    //     this.node.scale.y = scale;
    //     //this.height = height;
    //     this.updateUV(scale);
    // }
    // setEffect(isEffect) {
    //     for(let i = 0; i < this.node.children.length; i++) {
    //         this.updateEffect(this.node.children[i], {
    //             //name: 'outline',
    //             name: 'urealBloom',
    //             value: 1
    //         }, isEffect);
    //     }
    // }
    // updateUV(height) {
    //     // for (let n in this.node.children) {
    //     //     let group = this.node.children[n].geometry.groups[1], 
    //     //         start = group.start * 2, 
    //     //         count = group.count * 2, 
    //     //         uv = this.node.children[n].geometry.attributes.uv2.array, 
    //     //         h = height;
    //     //     for (let i = start; i < start + count; i += 2) {
    //     //         uv[i + 1] = uv[i + 1] * h;
    //     //     }
    //     //     let geometry = this.node.children[n].geometry;
    //     //     //geometry.setAttribute("uv", new THREE.BufferAttribute(uv, 2));
    //     //     geometry.setAttribute("uv2", new THREE.BufferAttribute(uv, 2));
    //     //     //geometry.attributes.uv.needsUpdate = true;
    //     // }
    // }
}