import * as THREE from 'three';
import GlobeVS from '../Shaders/GlobeVS.glsl';
import GlobeFS2 from '../Shaders/GlobeFS2.glsl';
import { createUniform } from './MaterialUtil';

const uniforms = createUniform();
uniforms.TexSampler1 = {
    value: {}
};
uniforms.alphaIndex1 = {
    value: 1
};
uniforms.textureRectangle1 = {
    value: new THREE.Vector4(0, 0, 0, 0)
};
uniforms.textureTransAndScale1 = {
    value: new THREE.Vector4(0, 0, 0, 0)
};
uniforms.brightness1 = {
    value: 1
};
uniforms.isAddEffect1 = {
    value: 0
};
uniforms.earthColor1 = {
    value: new THREE.Vector4(1, 1, 1, 1)
};
uniforms.earthAddColor0 = {
    value: new THREE.Vector4(0, 0, 0, 0)
};
uniforms.south2 = {
    value: 0
};
uniforms.north2 = {
    value: 0
};
uniforms.southMercatorY2 = {
    value: 0
};
uniforms.oneOverMercatorHeight2 = {
    value: 0
};

const GlobeMaterial2 = new THREE.ShaderMaterial({
    uniforms,
    vertexShader: GlobeVS,
    fragmentShader: GlobeFS2
});
GlobeMaterial2.fog = true;
GlobeMaterial2.lights = true;
export default GlobeMaterial2;