const Utils = {
    //TOLERANCE: 0.00001,
    //radius: 6378000,
    radius: Consts.RADIUS,
    normalizeVector: function (vec) {
        let a, b;
        if (vec.length === 4) {
            a = vec[0] * vec[0] + vec[1] * vec[1] + vec[2] * vec[2];
            b = Math.sqrt(a);
            vec[0] = vec[0] !== 0 ? vec[0] / b : 0;
            vec[1] = vec[1] !== 0 ? vec[1] / b : 0;
            vec[2] = vec[2] !== 0 ? vec[2] / b : 0;
            vec[3] = vec[3] !== 0 ? vec[2] / b : 0;
        } else {
            a = vec[0] * vec[0] + vec[1] * vec[1] + vec[2] * vec[2];
            b = Math.sqrt(a);
            vec[0] = vec[0] !== 0 ? vec[0] / b : 0;
            vec[1] = vec[1] !== 0 ? vec[1] / b : 0;
            vec[2] = vec[2] !== 0 ? vec[2] / b : 0;
        }
        return vec;
    },
    sub: function (a, b, c) {
        if (c === null || c === undefined) {
            c = [0, 0, 0];
        }
        c[0] = a[0] - b[0];
        c[1] = a[1] - b[1];
        c[2] = a[2] - b[2];
        return c;
    },
    subtractVectors: function (a, b, c) {
        if (a.isVector3) {
            a = [a.x, a.y, a.z];
        }
        if (b.isVector3) {
            b = [b.x, b.y, b.z];
        }
        return this.sub(a, b, c);
    },
    lonlatToWorldHeight: function (lonLat, radius) {
        let lon = lonLat[0];
        let lat = lonLat[1];
        let theta = lon / 180 * Math.PI;
        let phi = lat / 180 * Math.PI;
        if(radius === null || radius === undefined) {
            radius = 0;
        }
        let s = radius * Math.cos(phi);
        let y = radius * Math.sin(phi);
        let x = -s * Math.cos(theta);
        let z = s * Math.sin(theta);
        return [x, y, z];
    },
    lonLatToWorld: function (lonLat, radius) {
        return this.lonlatToWorldHeight(lonLat, this.radius + (radius || 0))
        // let lon = lonLat[0];
        // let lat = lonLat[1];
        // let theta = lon / 180 * Math.PI;
        // let phi = lat / 180 * Math.PI;
        // if(radius === null || radius === undefined) {
        //     radius = this.radius;
        // } else {
        //     radius = this.radius + radius;
        // }
        // let s = radius * Math.cos(phi);
        // let y = radius * Math.sin(phi);
        // let x = -s * Math.cos(theta);
        // let z = s * Math.sin(theta);
        // return [x, y, z];
    },
    worldToLonLat: function (pos, op) {
        let t = op;
        if (!t) {
            t = [0, 0, 0];
        }
        let r = this.subtractVectors(pos, t),
            n = r[1],
            i = r[2],
            o = [r[0], 0, r[2]];
        r = this.normalizeVector(r);
        o = this.normalizeVector(o);
        let a = 180 - this.getAngleBetweenVectors(o, [1, 0, 0]),
            s = this.getAngleBetweenVectors(o, r);
        if (i < 0) {
            a = -a;
        }
        if (n < 0) {
            s = -s;
        }
        return [a, s, this.vectorDistance(pos, t) - this.radius];
    },
    // worldToLonLat: function(aPos, b) {
    //     let bPos = b || [0, 0, 0];
    //     let r = this.subtractVectors(aPos, bPos),
    //         n = r[1],
    //         i = r[2],
    //         o = [r[0], 0, r[2]];
    //     r = this.normalizeVector(r),
    //         o = this.normalizeVector(o);
    //     let a = 180 - this.getAngleBetweenVectors(o, [1, 0, 0]),
    //         s = this.getAngleBetweenVectors(o, r);
    //     if (i < 0) {
    //         a = -a;
    //     }
    //     if (n < 0) {
    //         s = -s;
    //     }
    //     return [a, s, this.vectorDistance(aPos, bPos) - this.radius];
    // },
    // convertWorldToLonLat: function(pos) {
    //     return this.worldToLonLat(pos);
    // },
    // convertLonLatToWorld: function(a, b) {
    //     if (a.length === 3 && b === undefined) {
    //         b = a[2];
    //     }
    //     return this.convertLonLatToWorld(a, b);
    // },
    // convertLonLatToWorld: function (a, b = 0) {
    //     // if(b === null || b === undefined) {
    //     //     b = 0;
    //     // }
    //     return this.lonLatToWorld(a, b + this.radius);
    // },
    lonLatToWebMercator: function(pos) {
        return this.lonLatToMercator(pos);
    },
    lonLatToMercator: function (pos) {
        let x = 20037508.34 * pos[0] / 180;
        let y = Math.log(Math.tan((90 + pos[1]) * Math.PI / 360)) / (Math.PI / 180);
        y = 20037508.34 * y / 180;
        if (y > 20037508.34) {
            y = 20037508.34;
        }
        if (y < -20037508.34) {
            y = -20037508.34;
        }
        return [x, y];
    },
    arrayToFloat32Array: function (arr) {
        let len = arr.length;
        let rs = new Float32Array(len);
        for (let i = 0; i < len; i++) {
            rs[i] = arr[i];
        }
        return rs;
    },
    getDistanceFromArray: function (a, b) {
        return Math.sqrt((a[0] - b[0]) * (a[0] - b[0]) + (a[1] - b[1]) * (a[1] - b[1]));
    },
    cross: function (a, b, rs) {
        if (rs === null || rs === undefined) {
            rs = [0, 0, 0];
        }
        rs[0] = a[1] * b[2] - a[2] * b[1];
        rs[1] = a[2] * b[0] - a[0] * b[2];
        rs[2] = a[0] * b[1] - a[1] * b[0];
        return rs;
    },
    dot: function (a, b) {
        return a.x * b.x + a.y * b.y + a.z * b.z;
    },
    vectorDotProduct: function (a, b) {
        return a[0] * b[0] + a[1] * b[1] + a[2] * b[2];
    },
    vectorDistance: function (a, b) {
        let pos = this.subtractVectors(a, b);
        return this.vectorLength(pos);
    },
    vectorLength: function (vec) {
        return Math.sqrt(vec[0] * vec[0] + vec[1] * vec[1] + vec[2] * vec[2]);
    },
    vectorCrossProduct: function (a, b, c) {
        return this.cross(a, b, c);
    },
    radiansToDegrees: function (radians) {
        return radians / (Math.PI * 2) * 360;
    },
    getAngleBetweenVectors: function (aVec, bVec) {
        let rs = this.vectorDotProduct(aVec, bVec);
        // if (rs > 1 && rs < 1 + this.TOLERANCE) {
        //     rs = 1;
        // } else if (rs < -1 && rs > -1 - this.TOLERANCE) {
        //     rs = -1;
        // }
// if(rs > 1 || rs < 1) {
//     rs = 1;
// }        
        return this.radiansToDegrees(Math.acos(rs));
    },
    getCenterCoord: function(coord) {
        let x = 0;
        let y = 0;
        for(let i = 0; i < coord.length; i++) {
            x += coord[i][0];
            y += coord[i][1];
        }
        x /= coord.length;
        y /= coord.length;
        return [x, y];
    },
    isVisible: function(mesh) {
        return !!(1 & mesh.layers.mask);
    },
    setVisible: function(mesh, is) {
        if(is) {
            mesh.layers.enable(0);
        } else {
            mesh.layers.disable(0);
        }
    },
    visible: function (mesh, param) {
        let t = !(arguments.length > 2 && arguments[2] !== undefined) || arguments[2];
        if (param === undefined) {
            if (mesh.userData.useVisibleOnly) {
                return mesh.visible;
            }
            for (let i = mesh; i; i = i.parent) {
                if (!i.visible) {
                    return false;
                }
            }
            return this.isVisible(mesh);
        }   
        if (typeof param !== 'boolean') {
            let r = mesh.userData;
            if (!r.alwaysPickable) {
                for (let n = mesh; n;) {
                    if (!n.visible) {
                        return false;
                    }
                    if (param && !n.layers.test(param.layers)) {
                        return false;
                    }
                    n = n.parent;
                }
            }
            return true;
        }
        let o = param;
        if(mesh.userData.useVisibleOnly) {
            mesh.visible = o;
        } else {
            this.setVisible(mesh, o);
            if(t) {
                mesh.visible = o;
            }
        }
    },
    setPosition: function(mesh, pos) {
        let vec3 = new THREE.Vector3(0, 0, 0);
        if(Array.isArray(pos)) {
            vec3.set(pos[0], pos[1], pos[2]);
        } else if(pos.isVector3) {
            vec3 = pos.clone();
        } else {
            vec3.set(pos.x, pos.y, pos.z);
        }
        mesh.position.copy(vec3);
        (mesh.parent || mesh).updateMatrixWorld();
    },
    setDirection: function(mesh, sourceArr, targetArr) {
        let vec3 = new THREE.Vector3(), source = new THREE.Vector3(sourceArr[0], sourceArr[1], sourceArr[2]);
        mesh.getWorldDirection(vec3);
        vec3.set(targetArr[0], targetArr[1], targetArr[2]);
        let parentWL = mesh.worldToLocal(source),
            newParentWL = mesh.worldToLocal(new THREE.Vector3(0, 0, 0)),
            p = parentWL.sub(newParentWL);
        p.normalize();
        let quaternion = new THREE.Quaternion();
        quaternion.setFromUnitVectors(vec3, p);
        quaternion.normalize();
        mesh.setRotationFromQuaternion(quaternion);
        mesh.updateMatrixWorld();
    },
    worldToLocal: function(mesh, pos) {
        let vec3;
        if(pos.isVector3) {
            vec3 = pos.clone();
        } else {
            vec3 = new THREE.Vector3(pos[0], pos[1], pos[2]);
        }
        let rs = mesh.worldToLocal(vec3);
        return [rs.x, rs.y, rs.z];
    },
    transPosition: function(posArr, dom, camera) {
        let wPos = new THREE.Vector3(posArr[0], posArr[1], posArr[2]);
        //let vector = wPos.unproject(camera);
        let pos = wPos.project(camera);
        let halfWidth = (dom.innerWidth || dom.offsetWidth) / 2;
        let halfHeight = (dom.innerHeight || dom.offsetHeight) / 2; 
        return {
            x: Math.round(pos.x * halfWidth + halfWidth),
            y: Math.round(-pos.y * halfHeight + halfHeight)
        };
    },
    getColor: function(color) {
        if(color.indexOf('rgba') !== -1 || color.indexOf('rgb') !== -1) {
            let start = color.indexOf('(');
            let _color = color.substring(start + 1, color.length - 1).split(',');
            return [Number(_color[0].trim()) / 255, Number(_color[1].trim()) / 255, Number(_color[2].trim()) / 255];
        }
        return [255, 255, 255];
    }
};
export default Utils;