import * as THREE from 'three';
import Layer from './Layer';
import Utils from '../Common/Utils';
import GeoJSON from '../Common/GeoJSON';
export default class PointLayer extends Layer {
    constructor(props) {
        super();
        this.style = {
            img: 'images/point/1.png',
            color: '#00ff00',
            opacity: 1.0,
            size: 300,
            lineColor: '#ffffff',
            lineOpacity: 1.0
        };
        Object.assign(this, props);
        this.init();
    }
    init() {
        this.material = new THREE.PointsMaterial({
            // sizeAttenuation: false,
            // alphaTest: .5,
            // vertexColors: THREE.VertexColors,
            //color: 0x00ff00,
            size: this.style.size,
            transparent: true,
            blending: THREE.AdditiveBlending,
            depthTest: true,
            depthWrite: false,
            //map: this.view.textureLoader.load('')
        });
        this.lineMaterial = new THREE.PointsMaterial({
            //color: 0xffffff,
            // sizeAttenuation: false,
            // alphaTest: .5,
            // vertexColors: THREE.VertexColors,
            // color: 0x00ff00,
            // size: 200,
            transparent: true,
            // blending: THREE.AdditiveBlending,
            // depthTest: true,
            // depthWrite: false,
            //map: this.view.textureLoader.load('')
        });
    }
    geoJSON(data) {
        GeoJSON.toGeometry(data, rs => {
            if(rs.type === 'MultiPoint' || rs.type === 'Point') {
                this.createMesh(rs.type === 'Point' ? [rs.coordinates] : rs.coordinates, {
                    height: rs.properties.height !== undefined && rs.properties.height !== null ? rs.properties.height : this.height
                });
            }
        });
    }
    setData(data) {
        this.clear();
        //this.geoJSON(data);
        let geometry = new THREE.BufferGeometry();
        let positions = new Float32Array(data.length * 3);
        for(let i = 0; i < data.length; i++) {
            let start = Utils.lonLatToWorld([data[i].lng, data[i].lat]);
            let pos = Utils.lonLatToWorld([data[i].lng, data[i].lat], 150);
            positions[3 * i] = pos[0];
            positions[3 * i + 1] = pos[1];
            positions[3 * i + 2] = pos[2];

            let linePositions = new Float32Array(6);
            linePositions[0] = start[0];
            linePositions[1] = start[1];
            linePositions[2] = start[2];
            linePositions[3] = pos[0];
            linePositions[4] = pos[1];
            linePositions[5] = pos[2];
            let lineGeometry = new THREE.BufferGeometry();
            lineGeometry.setAttribute('position', new THREE.BufferAttribute(linePositions, 3));
            let line = new THREE.Line(lineGeometry, this.lineMaterial);
            this.node.add(line);
        }
        geometry.setAttribute('position', new THREE.BufferAttribute(positions, 3));
        let mesh = new THREE.Points(geometry, this.material);
        mesh.sortParticles = true;
        this.node.add(mesh);
        //this.node.position.y = 600;
        this.setStyle(this.style);
    }
    createMesh(coordinates, config) {
    }
    setStyle(style) {
        let {
            img,
            color,
            opacity,
            size,
            lineColor,
            lineOpacity
        } = style;
        if(color !== undefined) {
            this.material.color.set(color);
        }
        if(opacity !== undefined) {
            this.material.opacity = opacity;
        }
        if(size !== undefined) {
            this.material.size = size;
        }
        if(img !== undefined) {
            this.material.map = this.view.textureLoader.load(img);
        }
        if(lineColor !== undefined) {
            this.lineMaterial.color.set(lineColor);
        }
        if(lineOpacity !== undefined) {
            this.lineMaterial.opacity = lineOpacity;
        }
    }
}