import * as THREE from 'three';
import Viewport from './Viewport';
export default class Camera {
    constructor(props) {
        Object.assign(this, props);
        this.init();
    }
    init() {
        this.raycaster = new THREE.Raycaster();
        this.width = this.el.offsetWidth;
        this.height = this.el.offsetHeight;
        this.lookAtArr = [0, 0, 0];
        this.lastPos = [];
        this.lastRay = {};
    }
    getFov() {
        return this.camera.fov;
    }
    setPosition(inX, inY, inZ) {
        let x, y, z;
        if (Array.isArray(arguments[0])) {
            x = inX[0];
            y = inX[1];
            z = inX[2];
        } else {
            x = inX;
            y = inY;
            z = inZ;
        }
        let cameraPos = new THREE.Vector3(x, y, z);
        this.camera.position.copy(cameraPos);
    }
    // setPosition(a, b, c) {
    //     let x, y, z;
    //     if (Array.isArray(arguments[0])) {
    //         x = a[0];
    //         y = a[1];
    //         z = a[2];
    //     } else {
    //         x = a;
    //         y = b;
    //         z = c;
    //     }
    //     let pos = new THREE.Vector3(x, y, z);
    //     this.camera.position.copy(pos);
    // }
    getPosition() {
        let pos = new THREE.Vector3();
        pos.copy(this.camera.position);
        return [pos.x, pos.y, pos.z];
    }
    getLookAt() {
        return this.lookAtArr;
    }
    // setLookAt(x, y, z) {
    //     this.lookAt(x, y, z);
    // }
    lookAt(a, b, c) {
        let x, y, z;
        if (Array.isArray(arguments[0])) {
            x = a[0];
            y = a[1];
            z = a[2];
        } else {
            x = a;
            y = b;
            z = c;
        }
        this.lookAtArr = [x, y, z];
        let pos = new THREE.Vector3(x, y, z);
        this.camera.lookAt(pos);
    }
    // getSceneManager() { }
    // setAutoTracking() { }
    getDerivedPosition() {
        let pos = this.camera.position;
        return [pos.x, pos.y, pos.z];
    }
    getDerivedDirection() {
        let direction = new THREE.Vector3();
        this.camera.getWorldDirection(direction);
        return [direction.x, direction.y, direction.z];
    }
    getCameraToViewportRay(pos) {

        // let mouse = new THREE.Vector2(Number(pos[0]), Number(pos[1]));
        // 
        if (this.lastPos.toString() == pos.toString()) {
            return this.lastRay;
        }
        let x = pos[0];
        let y = pos[1];
        let mouse = new THREE.Vector2();
        mouse.x = x / this.width * 2 - 1;
        mouse.y = -y / this.height * 2 + 1;

        this.raycaster.setFromCamera(mouse, this.camera);
        let ray = {
            ori: [this.raycaster.ray.origin.x, this.raycaster.ray.origin.y, this.raycaster.ray.origin.z],
            dir: [this.raycaster.ray.direction.x, this.raycaster.ray.direction.y, this.raycaster.ray.direction.z]
        };
        this.lastRay = ray;
        return ray;
    }
    // getCameraToViewportRay(e) {
    //     if (this.lastPos.toString() == e.toString()) {
    //         return this.lastRay;
    //     }
    //     let t = e[0],
    //         r = e[1],
    //         n = new THREE.Vector2();
    //     n.x = t / this.width * 2 - 1;
    //     n.y = -r / this.height * 2 + 1;
    //     this.raycaster.setFromCamera(n, this.camera);
    //     var i = {
    //         ori: [this.raycaster.ray.origin.x, this.raycaster.ray.origin.y, this.raycaster.ray.origin.z],
    //         dir: [this.raycaster.ray.direction.x, this.raycaster.ray.direction.y, this.raycaster.ray.direction.z]
    //     };
    //     this.lastRay = i;
    //     return i;
    // }
    getViewport() {
        return new Viewport({
            camera: this.camera,
            el: this.el
        });
    }
    // isGoing() {
    //     return true;
    // }
    // stopGoing() { }
    moveRelative(inX, inY, inZ) {
        let x, y, z;
        if (Array.isArray(arguments[0])) {
            x = inX[0];
            y = inX[1];
            z = inX[2];
        } else {
            x = inX;
            y = inY;
            z = inZ;
        }
        this.camera.translateX(x);
        this.camera.translateY(y);
        this.camera.translateZ(z);
        /*
        let old = this.getPosition();
        let nx = old[0] + dx;
        let ny = old[1] + dy;
        let nz = old[2] + dz;
        this.setPosition(nx, ny, nz);*/
    }
    getScreenCoords(pos) {
        let vector = new THREE.Vector3(pos[0], pos[1], pos[2]);
        vector.project(this.camera);

        let widthHalf = this.width / 2,
            heightHalf = this.height / 2;
        vector.x = vector.x * widthHalf + widthHalf;
        vector.y = -vector.y * heightHalf + heightHalf;
        return [vector.x, vector.y];
    }
    getSpriteScreenCoords(pos) {
        let vector = new THREE.Vector3(pos[0], pos[1], pos[2]);
        vector.project(this.camera);

        let widthHalf = this.width / 2,
            heightHalf = this.height / 2;
        vector.x = vector.x * widthHalf;
        vector.y = vector.y * heightHalf;
        return [vector.x, vector.y];
    }
    // getScreenCoords(e) {
    //     let t = new THREE.Vector3(e[0], e[1], e[2]);
    //     t.project(this.camera);
    //     let r = this.width / 2, n = this.height / 2;
    //     t.x = t.x * r + r;
    //     t.y = -t.y * n + n;
    //     return [t.x, t.y];
    // }
    // getSpriteScreenCoords(e) {
    //     let t = new THREE.Vector3(e[0], e[1], e[2]);
    //     t.project(this.camera);
    //     let r = this.width / 2, n = this.height / 2;
    //     t.x = t.x * r;
    //     t.y = t.y * n;
    //     return [t.x, t.y];
    // }
    localToWorldPos(mesh, pos) {
        let p;
        if (pos.isVector3) {
            p = pos.clone();
        } else {
            p = new THREE.Vector3(pos[0], pos[1], pos[2]);
        }
        let rs = mesh.localToWorld(p);
        return [rs.x, rs.y, rs.z];
    }
    getDirection() {
        let direction = new THREE.Vector3();
        this.camera.getWorldDirection(direction);
        return [direction.x, direction.y, direction.z];
    }
    getUp() {
        let p1 = this.localToWorldPos(this.camera, [0, 1, 0]),
            p2 = this.localToWorldPos(this.camera, [0, 0, 0]);
        return [p1[0] - p2[0], p1[1] - p2[1], p1[2] - p2[2]];
    }
    setNear(near) {
        if (this.camera.type === "PerspectiveCamera") {
            this.camera.near = near;
        } else {
            this.camera.setNear(near);
        }
        if (this.camera.updateProjectionMatrix) {
            this.camera.updateProjectionMatrix();
        }
    }
    setFar(far) {
        if (this.camera.type === "PerspectiveCamera") {
            this.camera.far = far;
        } else {
            this.camera.setFar(far);
        }
        if (this.camera.updateProjectionMatrix) {
            this.camera.updateProjectionMatrix();
        }
    }
    setNearClipDistance(near) {
        if (this.camera.type === "PerspectiveCamera") {
            this.camera.near = near;
        } else {
            this.camera.setNear(near);
        }
        if (this.camera.updateProjectionMatrix) {
            this.camera.updateProjectionMatrix();
        }
    }
    setFarClipDistance(far) {
        if (this.camera.type === "PerspectiveCamera") {
            this.camera.far = far;
        } else {
            this.camera.setFar(far);
        }
        if (this.camera.updateProjectionMatrix) {
            this.camera.updateProjectionMatrix();
        }
    }
    setFixedYawAxis(useFixed, fixedAxis) {
        if (useFixed) {
            this.camera.up.set(fixedAxis[0], fixedAxis[1], fixedAxis[2]);
        } else {
            this.camera.up.set(0, 1, 0);
        }
    }
    // getCameraInfo() {
    //     let pos = this.camera._getWorldPosition(),
    //         cameraPos = [pos.x, pos.y, pos.z],
    //         viewPort = this.getViewport(),
    //         fov = this.getFov(),
    //         matrixWorld = this.camera._getMatrixWorld(),
    //         direction = new THREE.Vector3(),
    //         element = this.camera._getMatrixWorld().elements;
    //     direction.set(element[8], element[9], element[10]).normalize();
    //     let cameraDirection = [-direction.x, -direction.y, -direction.z],
    //         p1 = new THREE.Vector3(0, 1, 0).applyMatrix4(matrixWorld),
    //         p2 = new THREE.Vector3(0, 0, 0).applyMatrix4(matrixWorld);
    //     return {
    //         cameraPos,
    //         viewPort,
    //         fov,
    //         cameraDirection,
    //         cameraUp: [p1.x - p2.x, p1.y - p2.y, p1.z - p2.z]
    //     }
    // }
    set target(target) {
        this.control.target.fromArray(target);
    }
    get target() {
        return this.control.target.toArray();
    }
    set position(position) {
        this.camera.position.fromArray(position);
    }
    get position() {
        return this.camera.position.toArray();
    }
    get direction() {
        let rs = new THREE.Vector3();
        rs.subVectors({
            x: this.target[0],
            y: this.target[1],
            z: this.target[2]
        }, {
            x: this.position[0],
            y: this.position[1],
            z: this.position[2]
        });
        rs.normalize();
        return rs.toArray();
    }
    set up(up) {
        this.camera.up.fromArray(up);
    }
    get up() {
        return this.camera.up.toArray();
    }
}