import * as THREE from 'three';
import * as Cesium from 'cesium';
import Layer from './Layer';
import Utils from '../Common/Utils';
import GeoJSON from '../Common/GeoJSON';
export default class PolygonLayer extends Layer {
    constructor(props) {
        super();
        // this.effect = false;
        // this.color = 0xc9c9c9;
        // this.outlineColor = 0xff320f;
        // this.height = 600;
        this.style = {
            effect: false,
            opacity: .5,
            color: '#DD3217',
            outlineColor: '#ffa108',
            outlineOpacity: 1.0,
            height: 2000
        };
        Object.assign(this, props);
        this.init();
    }
    init() {
        this.material = new THREE.MeshBasicMaterial({
            color: this.style.color,
            // side: THREE.BackSide,
            // map: this.style.img,
            transparent: true,
            opacity: this.style.opacity
        });
        //this.lineGeometry = new THREE.BufferGeometry();
        this.lineMaterial = new THREE.MeshBasicMaterial({
            color: this.style.outlineColor,
            opacity: this.style.outlineOpacity
        });
    }
    geoJSON(data) {
        GeoJSON.toGeometry(data, rs => {
            if(rs.type === 'MultiPolygon' || rs.type === 'Polygon') {
                this.createMesh(rs.type === 'Polygon' ? [rs.coordinates] : rs.coordinates, {
                    //height: rs.properties.height !== undefined && rs.properties.height !== null ? rs.properties.height : this.style.height
                    height: this.style.height
                });
            }
        });
    }
    setData(data) {
        this.clear();
        this.geoJSON(data);
        this.setStyle(this.style);
    }
    createMesh(coordinates, config) {
        const node = new THREE.Group();
        this.node.add(node);
        let { height } = config;
        for(let i = 0; i < coordinates.length; i++) {
            let outers = [], holes = [];
            for(let j = 0; j < coordinates[i].length; j++) {
                if(j === 0) {
                    for(let m = 0; m < coordinates[i][j].length; m++) {
                        let point = Utils.lonLatToWorld(coordinates[i][j][m]);
                        outers.push({
                            x: point[0],
                            y: point[1],
                            z: point[2]
                        });
                    }
                } else {
                    let points = [];
                    for(let m = 0; m < coordinates[i][j].length; m++) {
                        let point = Utils.lonLatToWorld(coordinates[i][j][m]);
                        points.push({
                            x: point[0],
                            y: point[1],
                            z: point[2]
                        });
                    }
                    let hole = new Cesium.PolygonHierarchy(points);
                    holes.push(hole);
                }
            }
            let gm = new Cesium.PolygonHierarchy(outers, holes);
            let geometry = new Cesium.PolygonGeometry({
                polygonHierarchy: gm,
                extrudedHeight: height
            });
            geometry = Cesium.PolygonGeometry.createGeometry(geometry);
            let indices = geometry.indices;
            let positions = geometry.attributes.position.values;
            let positionsArr = new Float32Array(positions.length);
            let uv = geometry.attributes.st.values;
            let normals = geometry.attributes.normal.values;
            for(let k = 0; k < positions.length; k++) {
                let pos = [positions[3 * k], positions[3 * k + 1], positions[3 * k + 2]];
                positionsArr[3 * k] = pos[0];
                positionsArr[3 * k + 1] = pos[1];
                positionsArr[3 * k + 2] = pos[2];
            }
            geometry = new THREE.BufferGeometry();
            geometry.setIndex(new THREE.BufferAttribute(indices, 1));
            geometry.setAttribute('position', new THREE.BufferAttribute(positionsArr, 3));
            geometry.setAttribute('uv', new THREE.BufferAttribute(uv, 2));
            geometry.setAttribute('normal', new THREE.BufferAttribute(normals, 3));
            // new THREE.MeshBasicMaterial({
            //     map: null,
            //     transparent: true,
            //     opacity: 1.0,
            //     color: new THREE.Color(0xffffff).setHSL(Math.floor(Math.random() * 30 + 30) / 30, 0.6, 0.6)
            // })
            const obj = new THREE.Mesh(geometry, this.material);
            node.add(obj);
            let lineGeometry = new Cesium.PolygonOutlineGeometry({
                polygonHierarchy: new Cesium.PolygonHierarchy(gm.positions),
                extrudedHeight: height
            });
            lineGeometry = new Cesium.PolygonOutlineGeometry.createGeometry(lineGeometry);
            let linePositions = lineGeometry.attributes.position.values;

            lineGeometry = new THREE.BufferGeometry();
            lineGeometry.setAttribute('position', new THREE.Float32BufferAttribute(linePositions, 3));
            let line = new THREE.Line(lineGeometry, this.lineMaterial);
            node.add(line);
        }
        // this.setEffect(this.style.effect);
    }
    setStyle(style) {
        const {
            color,
            opacity,
            height,
            outlineColor,
            outlineOpacity,
            effect
        } = style;
        if(color !== undefined) {
            this.material.color.set(color);
        }
        if(opacity !== undefined) {
            this.material.opacity = opacity;
        }
        if(outlineColor !== undefined) {
            this.lineMaterial.color.set(outlineColor);
        }
        if(outlineOpacity !== undefined) {
            this.lineMaterial.opacity = outlineOpacity;
        }
        if(height !== undefined) {
            if(this.preHeight === undefined || this.preHeight === null) {
                this.preHeight = height;
            }
            let scale = height / this.preHeight;
            this.node.children.forEach(item => {
                item.scale.y = scale || 1;
            });
            //this.node.scale.y = scale;
        }
        this.setEffect(effect);
        // if(effect !== undefined) {
        //     for(let i = 0; i < this.node.children.length; i++) {
        //         this.updateEffect(this.node.children[i], {
        //             //name: 'outline',
        //             name: 'urealBloom',
        //             value: 1
        //         }, effect);
        //     }
        // }
    }
    setEffect(isEffect) {
        for(let i = 0; i < this.node.children.length; i++) {
            for(let j = 0; j < this.node.children[i].children.length; j++) {
                this.updateEffect(this.node.children[i].children[j], {
                    //name: 'outline',
                    name: 'urealBloom',
                    value: 0.3
                }, isEffect);
            }
        }
    }
    // updateColor(color) {
    //     for(let i = 0; i < this.material.length; i++) {
    //         this.material[i].color.setHex(color);
    //     }
    //     this.lineMaterial.color.setHex(color);
    // }
    // updateOpacity(opacity) {
    //     for(let i = 0; i < this.material.length; i++) {
    //         this.material[i].opacity = opacity;
    //     }
    // }
    // updateHeight(height) {
    //     const scale = height / this.height;
    //     this.node.scale.y = scale;
    //     //this.height = height;
    //     this.updateUV(scale);
    // }
    // setEffect(isEffect) {
    //     for(let i = 0; i < this.node.children.length; i++) {
    //         this.updateEffect(this.node.children[i], {
    //             //name: 'outline',
    //             name: 'urealBloom',
    //             value: 1
    //         }, isEffect);
    //     }
    // }
    // updateUV(height) {
    //     // for (let n in this.node.children) {
    //     //     let group = this.node.children[n].geometry.groups[1], 
    //     //         start = group.start * 2, 
    //     //         count = group.count * 2, 
    //     //         uv = this.node.children[n].geometry.attributes.uv2.array, 
    //     //         h = height;
    //     //     for (let i = start; i < start + count; i += 2) {
    //     //         uv[i + 1] = uv[i + 1] * h;
    //     //     }
    //     //     let geometry = this.node.children[n].geometry;
    //     //     //geometry.setAttribute("uv", new THREE.BufferAttribute(uv, 2));
    //     //     geometry.setAttribute("uv2", new THREE.BufferAttribute(uv, 2));
    //     //     //geometry.attributes.uv.needsUpdate = true;
    //     // }
    // }
}