import * as THREE from 'three';
import Layer from './Layer';
import Utils from '../Common/Utils';
import GeoJSON from '../Common/GeoJSON';
export default class LineLayer extends Layer {
    constructor(props) {
        super();
        this.style = {
            speed: 0.02,
            width: 5,
            color: '#ffc627',
            opacity: 1,
            imageUrl: 'images/path/path.png',
            effect: true
        };
        Object.assign(this, props);
        this.init();
    }
    init() {
        const texture = this.view.textureLoader.load(this.style.imageUrl);
        texture.wrapS = THREE.RepeatWrapping;
        let lineMaterial = new THREE.MeshBasicMaterial({
            color: this.style.color,
            map: texture,
            //side: THREE.DoubleSide,
            transparent: true,
            depthWrite: false,
            //depthTest: false
            opacity: this.opacity
        });
        this.material = lineMaterial;
    }
    geoJSON(data) {
        GeoJSON.toGeometry(data, rs => {
            if(rs.type === 'MultiLineString' || rs.type === 'LineString') {
                this.createMesh(rs.type === 'LineString' ? [rs.coordinates] : rs.coordinates);
            }
        });
    }
    setData(data) {
        this.clear();
        this.geoJSON(data);
        this.setStyle(this.style);
    }
    createMesh(coordinates) {
        //const coords = this.geojsonToPolygon(coordinates);
        //const center = this.getCenterCoordinates(coords[0][0]);
        //const centerPos = Utils.lonLatToWorld(center);
        //setPosition(this.node, centerPos);
        //setDirection(this.node, centerPos, [0, 1, 0]);
        // let lineMaterial = Cache.get('lineMaterial', 'test');
        // if(!lineMaterial) {        
        //     Cache.save('lineMaterial', 'test', this.material);
        // }
        //let node = new THREE.Group();
        //this.node.add(node);
        for(let i = 0; i < coordinates.length; i++) {
            //let positions = [];
            let item = coordinates[i];
            let path = [];
            for(let j = 0; j < item.length; j++) {
                let pos = Utils.lonLatToWorld(item[j]);
                //positions.push(pos[0]);
                //positions.push(pos[1]);
                //positions.push(pos[2]);
                path.push(new THREE.Vector3(pos[0], pos[1], pos[2]));
            }
            let curve3 = new THREE.CatmullRomCurve3(path);
            let geometry = new THREE.TubeBufferGeometry(curve3, 64, this.style.width, 8, false);
            geometry.attributes.uv.needsUpdate = true;
            let obj = new THREE.Mesh(geometry, this.material);
            //obj.renderOrder = 0;
            this.node.add(obj);
            // this.updateEffect(obj, {
            //     name: 'urealBloom',
            //     value: 1
            // }, true);
        }
        //this.setEffect(this.style.effect);
    }
    setStyle(style) {
        let {
            color,
            img,
            opacity,
            speed,
            direction,
            effect
        } = style;
        if(img) {
            this.material.map = this.view.textureLoader.load(img, item => {
                item.wrapS = THREE.RepeatWrapping;
                item.wrapT = THREE.RepeatWrapping;
                item.anisotropy = 16;
                item.needsUpdate = true;
                return item;
            });
        } else {
            this.material.map = this.view.textureLoader.load('images/path/path.png', item => {
                item.wrapS = THREE.RepeatWrapping;
                item.wrapT = THREE.RepeatWrapping;
                item.anisotropy = 16;
                item.needsUpdate = true;
                return item;
            });
        }
        if(color !== undefined) {
            this.material.color.set(color);
        }
        if(opacity !== undefined) {
            this.material.opacity = opacity;
        }
        if(direction !== undefined) {
            this.style.direction = direction;
        }
        if(speed !== undefined) {
            this.style.speed = speed;
        }
        this.setEffect(effect);
    }
    setEffect(isEffect) {
        for(let i = 0; i < this.node.children.length; i++) {
            this.updateEffect(this.node.children[i], {
                //name: 'outline',
                name: 'urealBloom',
                value: 1
            }, isEffect);
        }
    }
    updateColor(color) {
        this.material.color.setHex(color);
    }
    updateOpacity(opacity) {
        this.material.opacity = opacity;
    }
    update() {
        if(this.material.map) {
            if(this.style.direction) {
                this.material.map.offset.x -= this.style.speed;
            } else {
                this.material.map.offset.x += this.style.speed;
            }
            //this.material.map.offset.x -= 0.02;
        }
    }
}