import * as THREE from 'three';
import GlobeVS from '../Shaders/GlobeVS.glsl';
import GlobeFS3 from '../Shaders/GlobeFS3.glsl';
import { createUniform } from './MaterialUtil';

const uniforms = createUniform();
uniforms.TexSampler1 = {
    value: {}
};
uniforms.TexSampler2 = {
    value: {}
};
uniforms.alphaIndex1 = {
    value: 1
};
uniforms.alphaIndex2 = {
    value: 1
};
uniforms.textureRectangle1 = {
    value: new THREE.Vector4(0, 0, 0, 0)
};
uniforms.textureRectangle2 = {
    value: new THREE.Vector4(0, 0, 0, 0)
};
uniforms.textureTransAndScale1 = {
    value: new THREE.Vector4(0, 0, 0, 0)
};
uniforms.textureTransAndScale2 = {
    value: new THREE.Vector4(0, 0, 0, 0)
};
uniforms.brightness1 = {
    value: 1
};
uniforms.brightness2 = {
    value: 1
};
uniforms.isAddEffect1 = {
    value: 0
};
uniforms.isAddEffect2 = {
    value: 0
};
uniforms.earthColor1 = {
    value: new THREE.Vector4(1, 1, 1, 1)
};
uniforms.earthColor2 = {
    value: new THREE.Vector4(1, 1, 1, 1)
};
uniforms.earthAddColor1 = {
    value: new THREE.Vector4(0, 0, 0, 0)
};
uniforms.earthAddColor2 = {
    value: new THREE.Vector4(0, 0, 0, 0)
};
uniforms.south2 = {
    value: 0
};
uniforms.north2 = {
    value: 0
};
uniforms.southMercatorY2 = {
    value: 0
};
uniforms.oneOverMercatorHeight2 = {
    value: 0
};
uniforms.south3 = {
    value: 0
};
uniforms.north3 = {
    value: 0
};
uniforms.southMercatorY3 = {
    value: 0
};
uniforms.oneOverMercatorHeight3 = {
    value: 0
};
const GlobeMaterial3 = new THREE.ShaderMaterial({
    uniforms,
    vertexShader: GlobeVS,
    fragmentShader: GlobeFS3
});
GlobeMaterial3.fog = true;
GlobeMaterial3.lights = true;
export default GlobeMaterial3;