//import EffectManager from '../EffectManager';
export default class Layer {
    constructor(props) {

    }
    init() {
        
    }
    clear() {
        this.node.remove(...this.node.children);
    }
    updateEffect(mesh, config, isEffect) {
        const { name, value } = config;
        if(isEffect) {
            window.THREEx.EffectManager.add(mesh, name, value);
        } else {
            window.THREEx.EffectManager.remove(mesh, name);
        }
        
    }
}