import * as THREE from 'three';
const Cache = {
    init: function() {
        this.cacheList = THREE._____Cache = {};
    },
    save: function(cacheName, key, value) {
        if(!this.cacheList[cacheName]) {
            this.cacheList[cacheName] = new Map();
        }
        this.cacheList[cacheName].set(key, value);
    },
    get: function(cacheName, key) {
        if(this.cacheList[cacheName]) {
            return this.cacheList[cacheName].get(key);
        }
        return null;
    },
    delete: function(cacheName, key) {
        if(this.cacheList[cacheName]) {
            this.cacheList[cacheName].delete(key);
        }
    },
    getAll: function(cacheName) {
        if(this.cacheList[cacheName]) {
            return this.cacheList[cacheName];
        }
        return null;
    }
};
export default Cache;