import * as THREE from 'three';
import Utils from '../Common/Utils';
export default class CameraControl {
    constructor(props) {
        this.radius = Utils.radius;
        Object.assign(this, props);
    }
    flyTo(param) {
        let camera = this.camera, opt = param || {};
        let uniformSpeed = opt.uniformSpeed !== undefined && opt.uniformSpeed;
        if (opt.position && opt.target) {
            Object.assign(opt, this.getEarthCameraInfoByPositionAndTarget(opt.position, opt.target));
        }
        if (opt.time === undefined || opt.time === null) {
            opt.time = 2000;
        }
        opt.heading = opt.heading || 0;
        if (opt.pitch !== undefined && opt.pitch !== null) {
            opt.tilt = opt.pitch;
        }
        opt.tilt = opt.tilt || 45;
        opt.lonLat = opt.lonLat || [116.390834, 39.916353];
        //let height = opt.height;

        if (opt.object && opt.object.boundingBox) {
            opt.lonLat = this.worldToLonLat(opt.object.boundingBox.center);
            opt.height = opt.object.boundingBox.radius;
        }
        //  else if (opt.height !== undefined && opt.height !== null) {
        //     opt.height = 10000;
        // }
        // if(height || opt.height === 0) {
        //     opt.height = height;
        // }
        let cameraInfo = this.getEarthCameraInfo(),
            a = (e) => {
                return e / 10;
            },
            s = (h) => {
                let target = camera.target,
                    pos = this.lonLatToWorld(opt.lonLat),
                    from = new THREE.Vector3().fromArray(target),
                    to = new THREE.Vector3().fromArray(pos);
                return from.angleTo(to) * (h + this.radius);
            },
            u = (t) => {
                opt.speed = t / (opt.time / 1000) / 20;
            },
            c = null,
            _h = () => {
                let pos = this.lonLatToWorld(opt.lonLat);
                return new THREE.Vector3().fromArray(camera.target).distanceTo(new THREE.Vector3().fromArray(pos)) / this.radius * 2000000;
            },
            h = _h(),
            f = new THREE.Vector3().fromArray(camera.position).length() - this.radius,
            d = this.getCameraPositionAndTargetByLonLat(opt.lonLat, opt.height, opt.heading, opt.tilt),
            p = new THREE.Vector3().fromArray(camera.direction),
            m = new THREE.Vector3().subVectors(new THREE.Vector3().fromArray(camera.target), new THREE.Vector3().fromArray(d)),
            g = p.angleTo(m),
            v = this.lonLatToWorld(opt.lonLat),
            y = new THREE.Vector3().fromArray(d).distanceTo(new THREE.Vector3().fromArray(camera.position)),
            _ = new THREE.Vector3().fromArray(v).distanceTo(new THREE.Vector3().fromArray(camera.target));
        if (opt.time === 0) {
            camera.position = d;
            camera.target = v;
            this.correctUp();
            if (opt.complete) {
                opt.complete();
            }
            return;
        }
        if (y < 0.1 && _ < 0.1) {
            if (opt.complete) {
                opt.complete();
            }
        } else if (uniformSpeed) {
            this.directFlyTo(opt);
        } else {
            let x = Math.abs(opt.height - cameraInfo.height),
                b = 0,
                E = 0,
                w = 0;
            if (g < 0.8 && x > 0.5 * y || opt.directFly) {
                u((b = y / a((opt.height + f) / 2)) + E + w);
                this.directFlyTo(opt);
                return;
            }
            if (f < h) {
                if (opt.height < h) {
                    c = h;
                    u((b = Math.abs(c - f) / a((c + f) / 2)) + (E = s(c) / a(c)) + (w = Math.abs(c - opt.height) / a((c + opt.height) / 2)));
                } else {
                    c = opt.height;
                    u((b = Math.abs(c - f) / a((c + f) / 2)) + (E = s(c) / a(c)) + w);
                }
                this.directFlyTo({
                    lonLat: cameraInfo.lonLat,
                    height: c,
                    tilt: 45,
                    heading: cameraInfo.heading,
                    speed: opt.speed,
                    complete: () => {
                        this.rotateFlyTo(opt);
                    }
                })
            } else {
                u(b + (E = s(c = f) / a(c)) + (w = Math.abs(c - opt.height) / a((c + opt.height) / 2)));
                this.rotateFlyTo(opt);
            }
        }
    }
    normalizeVector(vec) {
        let a, b;
        if (vec.length === 4) {
            a = vec[0] * vec[0] + vec[1] * vec[1] + vec[2] * vec[2];
            b = Math.sqrt(a);
            vec[0] = vec[0] !== 0 ? vec[0] / b : 0;
            vec[1] = vec[1] !== 0 ? vec[1] / b : 0;
            vec[2] = vec[2] !== 0 ? vec[2] / b : 0;
            vec[3] = vec[3] !== 0 ? vec[2] / b : 0;
        } else {
            a = vec[0] * vec[0] + vec[1] * vec[1] + vec[2] * vec[2];
            b = Math.sqrt(a);
            vec[0] = vec[0] !== 0 ? vec[0] / b : 0;
            vec[1] = vec[1] !== 0 ? vec[1] / b : 0;
            vec[2] = vec[2] !== 0 ? vec[2] / b : 0;
        }
        return vec;
    }
    lonlatToWorldHeight(lonLat, radius) {
        let lon = lonLat[0];
        let lat = lonLat[1];
        let theta = lon / 180 * Math.PI;
        let phi = lat / 180 * Math.PI;
        if(radius === null || radius === undefined) {
            radius = 0;
        }
        let s = radius * Math.cos(phi);
        let y = radius * Math.sin(phi);
        let x = -s * Math.cos(theta);
        let z = s * Math.sin(theta);
        return [x, y, z];
    }
    lonLatToWorld(lonLat, radius) {
        return this.lonlatToWorldHeight(lonLat, this.radius + (radius || 0));
        // let lon = lonLat[0];
        // let lat = lonLat[1];
        // let theta = lon / 180 * Math.PI;
        // let phi = lat / 180 * Math.PI;
        // if(radius === null || radius === undefined) {
        //     radius = this.radius;
        // } else {
        //     radius = this.radius + radius;
        // }
        // let s = radius * Math.cos(phi);
        // let y = radius * Math.sin(phi);
        // let x = -s * Math.cos(theta);
        // let z = s * Math.sin(theta);
        // return [x, y, z];
    }
    worldToLonLat(pos, op) {
        let t = op;
        if(!t) {
            t = [0, 0, 0];
        }
        let r = this.subtractVectors(pos, t), 
            n = r[1], 
            i = r[2], 
            o = [r[0], 0, r[2]];
        r = this.normalizeVector(r);
        o = this.normalizeVector(o);
        let a = 180 - this.getAngleBetweenVectors(o, [1, 0, 0]), 
            s = this.getAngleBetweenVectors(o, r);
        if(i < 0) {
            a = -a;
        }    
        if(n < 0) {
            s = -s;
        }
        return [a, s, this.vectorDistance(pos, t) - this.radius];
    }
    // convertLonLatToWorld(a, b = 0) {
    //     // if(b === null || b === undefined) {
    //     //     b = 0;
    //     // }
    //     return this.lonLatToWorld(a, b + this.radius);
    // }
    vectorDotProduct(a, b) {
        return a[0] * b[0] + a[1] * b[1] + a[2] * b[2];
    }
    sub(a, b, c) {
        if (c === null || c === undefined) {
            c = [0, 0, 0];
        }
        c[0] = a[0] - b[0];
        c[1] = a[1] - b[1];
        c[2] = a[2] - b[2];
        return c;
    }
    subtractVectors(a, b, c) {
        if (a.isVector3) {
            a = [a.x, a.y, a.z];
        }
        if (b.isVector3) {
            b = [b.x, b.y, b.z];
        }
        return this.sub(a, b, c);
    }
    vectorDistance(a, b) {
        let pos = this.subtractVectors(a, b);
        return this.vectorLength(pos);
    }
    vectorLength(vec) {
        return Math.sqrt(vec[0] * vec[0] + vec[1] * vec[1] + vec[2] * vec[2]);
    }
    cross(a, b, rs) {
        if (rs === null || rs === undefined) {
            rs = [0, 0, 0];
        }
        rs[0] = a[1] * b[2] - a[2] * b[1];
        rs[1] = a[2] * b[0] - a[0] * b[2];
        rs[2] = a[0] * b[1] - a[1] * b[0];
        return rs;
    }
    vectorCrossProduct(a, b, c) {
        return this.cross(a, b, c);
    }
    radiansToDegrees(radians) {
        return radians / (Math.PI * 2) * 360;
    }
    getAngleBetweenVectors(aVec, bVec) {
        let rs = this.vectorDotProduct(aVec, bVec);
        if (rs > 1) {
            rs = 1;
        } else if (rs < -1) {
            rs = -1;
        }
        // if (rs > 1 && rs < 1 + this.TOLERANCE) {
        //     rs = 1;
        // } else if (rs < -1 && rs > -1 - this.TOLERANCE) {
        //     rs = -1;
        // }
        return this.radiansToDegrees(Math.acos(rs));
    }
    getEarthCameraInfo() {
        let camera = this.camera,
            target = camera.target,
            pos = camera.position;
        return this.getEarthCameraInfoByPositionAndTarget(pos, target);
    }
    rotateByVector(a, b, c) {
        let m = new THREE.Vector3(a[0], a[1], a[2]),
            n = new THREE.Vector3(b[0], b[1], b[2]),
            rs = m.applyAxisAngle(n, c);
        return [rs.x, rs.y, rs.z];
    }
    getCameraPositionAndTargetByLonLat(lonLat, t, r, i) {
        let o = this.lonLatToWorld(lonLat, t),
            a = [o[0], o[1], o[2]],
            s = new THREE.Vector3(a[0], a[1], a[2]).normalize(),
            l = new THREE.Vector3(0, 1, 0).projectOnPlane(s);
        l = l.normalize();
        l = l.multiplyScalar(-1);
        let u = t / Math.tan(i / 180 * Math.PI),
            c = [o[0] + u * l.x, o[1] + u * l.y, o[2] + u * l.z],
            h = [s.x, s.y, s.z];
        c = this.rotateByVector(c, h, r / 180 * Math.PI);
        return c;
    }
    getEarthCameraInfoByPositionAndTarget(target, pos) {
        let rs = {};
        rs.lonLat = this.worldToLonLat(pos);
        rs.height = this.vectorDistance(target, [0, 0, 0]) - this.radius;
        if (pos[0] === 0 || pos[1] === 0 || pos[2] === 0) {
            return rs;
        }
        let distance = [target[0] - pos[0], target[1] - pos[1], target[2] - pos[2]];
        distance = this.normalizeVector(distance);
        let a = this.normalizeVector([pos[0], pos[1], pos[2]]),
            s = this.getAngleBetweenVectors(distance, a),
            l = [target[0] - pos[0], target[1] - pos[1], target[2] - pos[2]],
            u = this.vectorCrossProduct(a, l);
        u = this.normalizeVector(u);
        let c = this.vectorDotProduct([0, 1, 0], a),
            h = [a[0] * c, a[1] * c, a[2] * c],
            f = [0 - h[0], 1 - h[1], 0 - h[2]],
            d = this.normalizeVector(f),
            p = this.vectorCrossProduct(d, a),
            m = this.normalizeVector(p),
            g = this.getAngleBetweenVectors(u, m);
        if (this.getAngleBetweenVectors(u, d) < 90) {
            g = -g;
        }
        rs.heading = -g;
        rs.tilt = 90 - s;
        rs.pitch = rs.tilt;
        return rs;
    }
    getCameraUpByPositionAndTarget(a, b) {
        let r = [b[0] - a[0], b[1] - a[1], b[2] - a[2]],
            n = this.vectorCrossProduct(a, r),
            rs = this.vectorCrossProduct(r, n);
        rs = this.normalizeVector(rs);
        return rs;
    }
    correctUp() {
        let camera = this.camera;
        camera.up = this.normalizeVector(camera.position);
    }
    getSpeed(a, b) {
        let camera = this.camera;
        return (new THREE.Vector3().fromArray(camera.position).length() - b + 10) / 10 * a;
    }
    directFlyTo(param) {
        let camera = this.camera, opt = param || {};
        opt.speed = opt.speed || 1;
        opt.heading = opt.heading || 0;
        opt.tilt = opt.tilt || 45;
        opt.lonLat = opt.lonLat || [116.390834, 39.916353];
        opt.height = opt.height || 10000;
        let r = undefined === opt.disablePick || opt.disablePick;
        let i = opt.uniformSpeed, cameraInfo = this.getEarthCameraInfo();
        function a(e, t, r) {
            return Math.abs(e - t) < r;
        }
        if (a(cameraInfo.lonLat[0], opt.lonLat[0], 0.0001)
            && a(cameraInfo.lonLat[1], opt.lonLat[1], 0.0001)
            && a(cameraInfo.height, opt.height, 5)
            && a(cameraInfo.tilt, opt.tilt, 10)
            && a(cameraInfo.heading, opt.heading, 5)
        ) {
            if (opt.complete) {
                opt.complete();
            }
            return;
        }

        let s = camera.position, 
            u = camera.target, 
            c = this.lonLatToWorld(opt.lonLat), 
            h;
        if (opt.target) {
            c = opt.target;
        }
        if (opt.position) {
            h = opt.position;
        } else {
            let f = this.lonLatToWorld(opt.lonLat, opt.height),
                d = new THREE.Vector3(f[0], f[1], f[2]).normalize(),
                p = new THREE.Vector3(0, 1, 0).projectOnPlane(d);
            p = p.normalize();
            p = p.multiplyScalar(-1);
            let m = opt.height / Math.tan(opt.tilt / 180 * Math.PI);
            h = [f[0] + m * p.x, f[1] + m * p.y, f[2] + m * p.z];
            let g = [d.x, d.y, d.z];
            h = this.rotateByVector(h, g, opt.heading / 180 * Math.PI);
        }
        let v = [h[0] - s[0], h[1] - s[1], h[2] - s[2]],
            y = [c[0] - u[0], c[1] - u[1], c[2] - u[2]],
            _ = new THREE.Vector3().fromArray(v).length(),
            x = camera.up,
            b = this.getCameraUpByPositionAndTarget(h, c),
            E = [b[0] - x[0], b[1] - x[1], b[2] - x[2]];
        function w(e) {
            let r = [s[0] + v[0] * e, s[1] + v[1] * e, s[2] + v[2] * e],
                n = [u[0] + y[0] * e, u[1] + y[1] * e, u[2] + y[2] * e],
                i = [x[0] + E[0] * e, x[1] + E[1] * e, x[2] + E[2] * e];
                camera.position = r;
                camera.up = i;
                camera.target = n;
        }
        let T = 0, M = 0;
        //camera.inputEnabled = false;
        //camera.onStart();
        let R = this.vectorLength(c), S = new Date().getTime();
        this.onUpdate = () => {
            //camera.onChange();
            //camera.cameraObject.controls[0].control._flying = true;
            //if (r) {
                //this.picker._state.lock(false, "earthFlyParam");
            //}
            let n = 0;
            if (i) {
                let t = new Date().getTime();
                M = _ * (t - S) / opt.time;
            } else {
                n = this.getSpeed(opt.speed, R);
                M += n;
            }
            T = M / _;
            if (T < 1) {
                w(T);
            } else if (T >= 1) {
                //camera.onEnd();
                //camera.cameraObject.controls[0].control._flying = false;
                w(1);
                //camera.inputEnabled = true;
                //if (r) {
                    //this.picker._state.unlock("earthFlyParam");
                //}
                if (opt.complete) {
                    opt.complete();
                }
                this.onUpdate = null;
                //this.off("update", null, "directFlyToGeoPositionLoop");
            }
        }
        // this.on("update", function () {
        //     camera.onChange();
        //     camera.cameraObject.controls[0].control._flying = true;
        //     if (r) {
        //         this.picker._state.lock(false, "earthFlyParam");
        //     }
        //     let n = 0;
        //     if (i) {
        //         let t = new Date().getTime();
        //         M = _ * (t - S) / e.time;
        //     } else {
        //         n = this.getSpeed(e.speed, R);
        //         M += n;
        //     }
        //     T = M / _;
        //     if (T < 1) {
        //         w(T);
        //     } else if (T >= 1) {
        //         camera.onEnd();
        //         camera.cameraObject.controls[0].control._flying = false;
        //         w(1);
        //         camera.inputEnabled = true;
        //         if (r) {
        //             this.picker._state.unlock("earthFlyParam");
        //         }
        //         if (opt.complete) {
        //             opt.complete();
        //         }
        //         this.off("update", null, "directFlyToGeoPositionLoop");
        //     }
        // }, "directFlyToGeoPositionLoop")
    }
    rotateFlyTo(param) {
        let camera = this.camera, opt = param || {};
        opt.speed = opt.speed || 1;
        let r = camera.getPosition(),
            o = camera.target,
            a = this.lonLatToWorld(opt.lonLat),
            s = new THREE.Vector3().fromArray(o),
            u = new THREE.Vector3().fromArray(a),
            c = s.angleTo(u),
            h = new THREE.Vector3().crossVectors(s, u).normalize().toArray(),
            f = 0;
        //camera.inputEnabled = false;
        //camera.onStart();
        let d = new THREE.Vector3().fromArray(camera.position).length();
        let _p = () => {
            let r = new THREE.Vector3().fromArray(camera.position).length() - this.radius;
            if(r < 10) {
                r = 10;
            }
            return r / 10 * opt.speed;
        }
        let p = _p() / d;
        //console.log(p);
        this.preUpdate = () => {
            //camera.onChange();
            //camera.cameraObject.controls[0].control._flying = true;
            f += p;
            if (Math.abs(f) < Math.abs(c)) {
                let s = this.rotateByVector(r, h, f),
                    u = this.rotateByVector(o, h, f);
                camera.position = s;
                let e = this.getCameraUpByPositionAndTarget(s, u);
                camera.up = e;
                camera.target = u;
            } else if(Math.abs(f) >= Math.abs(c)) {
                this.preUpdate = null;
                //this.delete("earthFlyLoop");
                //camera.onEnd();
                //camera.cameraObject.controls[0].control._flying = false;
                camera.target = a;
                //camera.inputEnabled = true;
                this.directFlyTo(opt);
            }
        }
    }
    update() {
        if(this.preUpdate) {
            this.preUpdate();
        }
        if(this.onUpdate) {
            this.onUpdate();
        }
    }
}