if(!Cesium.__Custom) {
	Cesium.__Custom = {};
	Cesium.__Custom.ImageryReleaseReference = function(scope) {
		if (scope.image !== null && scope.image !== undefined) {
			Cache.delete('textureBuffer', scope.image.name);
		}            
		if (scope.texture !== null && scope.texture !== undefined) {
			scope.texture.dispose();
		}
	}
	Cesium.__Custom.ImageryLayerCreateTexture = function(imagery) {
		let image = imagery.image, src = image.src;
		image.name = encodeURI(src);
		let texture = Cache.get('textureBuffer', image.name);
		if(texture) {
			imagery.texture = texture;
		} else {
			if(true) {
				texture = new THREE.Texture(image);
				texture.needsUpdate = true;
				Cache.save('textureBuffer', image.name, texture);
				imagery.texture = texture;
				texture = null;
			} else {
				let canvas = document.createElement('canvas');
				canvas.width = 256;
				canvas.height = 256;
				//canvas.style.width = 256 / 2 + 'px';
				//canvas.style.height = 256 / 2 + 'px';
				let ctx = canvas.getContext('2d');
				ctx.clearRect(0, 0, canvas.width, canvas.height);
				ctx.filter = 'sepia(100%) invert(90%)';
				ctx.drawImage(image, 0, 0, canvas.width, canvas.height);
				texture = new THREE.Texture(canvas);
				texture.needsUpdate = true;
				Cache.save('textureBuffer', image.name, texture);
				imagery.texture = texture;
				texture = null;
				canvas = null;
				ctx = null;
			}
		}
	}
	Cesium.__Custom.TileReplacementQueueTrimTiles = function(tileToTrim) {
		let tileName = `EarthTile_${ tileToTrim._level }_${ tileToTrim._x }_${ tileToTrim._y }`, 
			mesh = Cache.get('nodeBuffer', tileName);
		if(mesh) {
			let materialName = mesh.materialName;
			Cache.get('tileMaterialBuffer', materialName).dispose();
			Cache.delete('tileMaterialBuffer', materialName);
			Cache.delete('nodeBuffer', tileName);
		}
	}
}

