	getFrameState(camera, mode) {
		let cameraPos = camera.cameraPos,
			viewPort = camera.viewPort,
			fov = camera.fov,
			cameraDirection = camera.cameraDirection,
			cameraUp = camera.cameraUp,
			frameState = this._frameState;
		frameState.cameraPos.x = cameraPos[0];
		frameState.cameraPos.y = -cameraPos[2];
		frameState.cameraPos.z = cameraPos[1];
		let positionWC = new Cesium.Cartesian3(), directionWC = new Cesium.Cartesian3(), upWC = new Cesium.Cartesian3();
		if (mode == this.sceneType.Scene_3D) {
			frameState.mapProjection = new Cesium.GeographicProjection();
			positionWC.x = -cameraPos[0];
			positionWC.y = cameraPos[2];
			positionWC.z = cameraPos[1];

			frameState.positionCartographic = frameState.mapProjection.ellipsoid.cartesianToCartographic(frameState.cameraPos);
			directionWC.x = -cameraDirection[0];
			directionWC.y = cameraDirection[2];
			directionWC.z = cameraDirection[1];

			upWC.x = -cameraUp[0];
			upWC.y = cameraUp[2];
			upWC.z = cameraUp[1];

			frameState.mode = Cesium.SceneMode.SCENE3D;
		} else if (mode == this.sceneType.Scene_2D) {
			frameState.mapProjection = new Cesium.WebMercatorProjection();
			positionWC.x = cameraPos[1];
			positionWC.y = cameraPos[0];
			positionWC.z = -cameraPos[2];

			let pos = new Cesium.Cartesian3();
			pos.x = cameraPos[0];
			pos.y = -cameraPos[2];
			pos.z = cameraPos[1];
			frameState.positionCartographic = frameState.mapProjection.unproject(pos);

			directionWC.x = cameraDirection[1];
			directionWC.y = cameraDirection[0];
			directionWC.z = -cameraDirection[2];

			upWC.x = cameraUp[1];
			upWC.y = cameraUp[0];
			upWC.z = -cameraUp[2];

			frameState.mode = Cesium.SceneMode.COLUMBUS_VIEW;
		}
		frameState.positionWC = positionWC;
		let width = viewPort.actualWidth, height = viewPort.actualHeight, aspectRatio = width / height, fovy;
		frameState.drawWidth = width;
		frameState.drawHeight = height;
		fov = this.getFov(aspectRatio, fov);
		fov = Math.PI * fov / 180;
		if (aspectRatio <= 1) {
			fovy = fov;
		} else {
			fovy = 2 * Math.atan(Math.tan(0.5 * fov) / aspectRatio);
		}
		frameState.fov = fov;
		frameState.fovy = fovy;
		frameState.aspectRatio = aspectRatio;
		frameState.sseDenominator = 2 * Math.tan(0.5 * fovy);
		frameState.perspectiveOffCenterFrustum.near = 1;
		frameState.perspectiveOffCenterFrustum.far = 500000000;
		frameState.perspectiveOffCenterFrustum.top = 1 * Math.tan(0.5 * fovy);
		frameState.perspectiveOffCenterFrustum.sseDenominator = frameState.sseDenominator;
		frameState.perspectiveOffCenterFrustum.bottom = -frameState.perspectiveOffCenterFrustum.top;
		frameState.perspectiveOffCenterFrustum.right = aspectRatio * frameState.perspectiveOffCenterFrustum.top;
		frameState.perspectiveOffCenterFrustum.left = -frameState.perspectiveOffCenterFrustum.right;
		frameState.directionWC = directionWC;
		frameState.upWC = upWC;

		frameState.cullingVolume = frameState.perspectiveOffCenterFrustum.computeCullingVolume(frameState.positionWC, directionWC, upWC);
		frameState.context = {};
		frameState.frameNumber = Cesium.Math.incrementWrap(frameState.frameNumber, 15000000, 1);
		frameState.afterRender = [];
		// if (!frameState.time) {
		// 	frameState.time = Cesium.JulianDate.fromDate(new Date());
		// }
		frameState.camera = {
			positionWC: frameState.positionWC,
			positionCartographic: frameState.positionCartographic,
			directionWC: frameState.directionWC,
			frustum: frameState.perspectiveOffCenterFrustum
		};
		frameState.context.drawingBufferWidth = frameState.drawWidth;
		frameState.context.drawingBufferHeight = frameState.drawHeight;
		frameState.shadowState.shadowsEnabled = false;
		//return frameState;
	}
