import * as THREE from 'three';
import GlobeVS from '../Shaders/GlobeVS.glsl';
import GlobeFS1 from '../Shaders/GlobeFS1.glsl';
import { createUniform } from './MaterialUtil';

const uniforms = createUniform();
const GlobeMaterial1 = new THREE.ShaderMaterial({
    uniforms,
    vertexShader: GlobeVS,
    fragmentShader: GlobeFS1
});
GlobeMaterial1.fog = true;
GlobeMaterial1.lights = true;
export default GlobeMaterial1;