import * as THREE from 'three';
import Layer from './Layer';
import Utils from '../Common/Utils';
import GeoJSON from '../Common/GeoJSON';
import { Children } from 'react';
export default class ScanLayer extends Layer {
    constructor(props) {
        super();
        //this.speed = .05;
        this.style = {
            height: 0,
            color: "#FF9400",
            opacity: 1.0,
            speed: 0.01
        };
        Object.assign(this, props);
        this.init();
    }
    init() {
        this.material = new THREE.ShaderMaterial({
            uniforms: {
                time: {
                    type: "f",
                    value: 0
                },
                color: {
                    type: "c",
                    value: new THREE.Color("#ff326e")
                },
                opacity: {
                    type: "f",
                    value: 1.0
                }
            },
            //defaultAttributeValues: {},
            vertexShader: `
                #include <common>
                #include <logdepthbuf_pars_vertex>
                //precision lowp float;
                //precision lowp int;
                varying vec2 vUv;
                void main() {
                    vec4 mvPosition = modelViewMatrix * vec4(position, 1.0);
                    vUv = uv;
                    gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
                    #include <logdepthbuf_vertex>
                }
            `,
            fragmentShader: `
                #include <common>
                #include <logdepthbuf_pars_fragment>
                //#if __VERSION__ == 100
                    //#extension GL_OES_standard_derivatives : enable
                //#endif
                uniform vec3 color;
                uniform float opacity;
                uniform float time;
                varying vec2 vUv;
                #define pi 3.1415926535
                #define PI2RAD 0.01745329252
                #define TWO_PI (2. * PI)
                float rands(float p) {
                    return fract(sin(p) * 10000.0);
                }
                float noise(vec2 p) {
                    float t = time / 20000.0;
                    if(t > 1.0) t -= floor(t);
                    return rands(p.x * 14. + p.y * sin(t) * 0.5);
                }
                vec2 sw(vec2 p) {
                    return vec2(floor(p.x), floor(p.y));
                }
                vec2 se(vec2 p) {
                    return vec2(ceil(p.x), floor(p.y));
                }
                vec2 nw(vec2 p) {
                    return vec2(floor(p.x), ceil(p.y));
                }
                vec2 ne(vec2 p) {
                    return vec2(ceil(p.x), ceil(p.y));
                }
                float smoothNoise(vec2 p) {
                    vec2 inter = smoothstep(0.0, 1.0, fract(p));
                    float s = mix(noise(sw(p)), noise(se(p)), inter.x);
                    float n = mix(noise(nw(p)), noise(ne(p)), inter.x);
                    return mix(s, n, inter.y);
                }
                float fbm(vec2 p) {
                    float z = 2.0;
                    float rz = 0.0;
                    vec2 bp = p;
                    for(float i = 1.0; i < 6.0; i++) {
                        rz += abs((smoothNoise(p) - 0.5)* 2.0) / z;
                        z *= 2.0;
                        p *= 2.0;
                    }
                    return rz;
                }
                void main() {
                    vec2 uv = vUv;
                    vec2 uv2 = vUv;
                    if (uv.y < 0.5) {
                        discard;
                    }
                    uv *= 4.;
                    float rz = fbm(uv);
                    uv /= exp(mod(time * 2.0, pi));
                    rz *= pow(15., 0.9);
                    gl_FragColor = mix(vec4(color, opacity) / rz, vec4(color, 0.1), 0.2);
                    if (uv2.x < 0.05) {
                        gl_FragColor = mix(vec4(color, 0.1), gl_FragColor, uv2.x / 0.05);
                    }
                    if (uv2.x > 0.95) {
                        gl_FragColor = mix(gl_FragColor, vec4(color, 0.1), (uv2.x - 0.95) / 0.05);
                    }
                    #include <logdepthbuf_fragment>
                }
            `,
            blending: THREE.AdditiveBlending,
            depthWrite: false,
            depthTest: true,
            side: THREE.DoubleSide,
            transparent: true,
            opacity: 1.0
        });
    }
    geoJSON(data) {
        GeoJSON.toGeometry(data, rs => {
            if(rs.type === 'MultiPoint' || rs.type === 'Point') {
                this.createMesh(rs.type === 'Point' ? [rs.coordinates] : rs.coordinates, {
                    height: rs.properties.height !== undefined && rs.properties.height !== null ? rs.properties.height : this.height
                });
            }
        });
    }
    setData(data) {
        this.clear();
        //this.geoJSON(data);
        for(let i = 0; i < data.length; i++) {
            let pos = Utils.lonLatToWorld([data[i].lng, data[i].lat]);
            let geometry = new THREE.SphereBufferGeometry(200, 200, 200, 200, Math.PI * 2);
            let obj = new THREE.Mesh(geometry, this.material);
            obj.position.x = pos[0];
            obj.position.y = pos[1];
            obj.position.z = pos[2];
            Utils.setDirection(obj, pos, [0, 1, 0]);
            this.node.add(obj);
        }
        this.setStyle(this.style);
    }
    // loadModel() {
    //     this.MTLLoader = new THREE.MTLLoader();
    //     this.OBJLoader = new THREE.OBJLoader();
    //     this.MTLLoader.setPath('model/sz/');
    //     this.MTLLoader.load('.mtl', materials => {
    //         materials.preload();
    //         //this.OBJLoader.setMaterials(materials);
    //         this.OBJLoader.setPath('model/sz/');
    //         this.OBJLoader.load('.obj', obj => {
    //             obj.traverse(children => {
    //                 if(children.isMesh) {
    //                     children.material.color.set(0x0c7f9c);
    //                 }
    //             });
    //             obj.position.x = _pos[0];
    //             obj.position.y = _pos[1];
    //             obj.position.z = _pos[2];
    //             //obj.scale.set(10, 10, 10);
    //             Utils.setDirection(obj, _pos, [0, 1, 0]);
    //             this.node.add(obj);
    //         });
    //     });
    // }
    createMesh(coordinates, config) {
    }
    setStyle(style) {
        let {
            height,
            color,
            opacity,
            speed
        } = style;
        if(color !== undefined) {
            this.material.uniforms.color.value.setStyle(color);
        }
        if(opacity !== undefined) {
            this.material.uniforms.opacity.value = opacity;
        }
        // if(height !== undefined) {
        //     this.node.position.y = height;
        // }
        if(speed !== undefined) {
            this.style.speed = speed;
        }
    }
    update() {
        if(this.material) {
            this.material.uniforms.time.value += this.style.speed;
        }
    }
}