	lerpToPerspective(e, t) {
		var i = this.toPerspectiveCamera();
		this.isPerspectiveLerping = true;
		var r = this;
		this.lerpMatrix({
			fromMatrix: this.projectionMatrix,
			toMatrix: i.projectionMatrix,
			time: e,
			stop: function() {
				r.isPerspectiveLerping = false;
				if(t) {
					t();
				}
			},
			complete: function() {
				r.isPerspectiveLerping = false;
				r.toPerspective();
				if(t) {
					t();
				}
			}
		})
	}
	getEffectiveFOV() {
		return this.cameraP.getEffectiveFOV();
	}
	isInView(e) {
		var t = new THREE.Matrix4;
		t.multiplyMatrices(this.projectionMatrix, this.matrixWorldInverse);
		var i = new THREE.Frustum;
		i.setFromMatrix(t);
		return i.intersectsBox(e);
	}
	updateMatrixWorld(e) {
		this.matrixAutoUpdate && this.updateMatrix();
		if(this.matrixWorldNeedsUpdate || e) {
			this.matrixWorld.copy(this.matrix);
		}
		//let t = ol();
		//if(t) {
			//this.matrixWorld.premultiply(t);
		//}
		this.matrixWorldInverse.getInverse(this.matrixWorld);
	}
