	get isPerspectiveCamera() {
		return this.inPerspectiveMode;
	}
	get isOrthographicCamera() {
		return this.inOrthographicMode;
	}
	get isLerping() {
		return this.isPerspectiveLerping || this.isOrthoLerping;
	}
