	resize() {
		let { width, height } = this.getSize();
		this.renderCamera.setSize(width, height);
		this.renderCamera.aspect = width / height;
		this.renderCamera.updateProjectionMatrix();
		this.renderer.setSize(width, height);
		this.CSSRender.setSize(width, height);
		this.compositorRender.update();
	}
	addMap(props) {
		if(!this.map) {
			const cls = require(`./Scene/Globe`).default;
			props.view = this;
			props.node = new THREE.Group();
			props.uid = THREE.Math.generateUUID();
			this.node.add(props.node);
			this.map = new cls(props);
			this.objectManager.set(this.map.uid, this.map);
		}
		return this.map;
	}
	setMap(mapConfig) {
		if(this.map) {
			this.map.setMap(mapConfig);
		} else {
			this.addMap(mapConfig);
		}
	}
	setStyle(style) {
		this.map.setStyle(style);
	}
	addLayer(props) {
		const cls = require(`./Layer/${props.type}`).default;
		props.view = this;
		props.node = new THREE.Group();
		props.uid = THREE.Math.generateUUID();
		this.node.add(props.node);
		const layer = new cls(props);
		this.objectManager.set(layer.uid, layer);
		return layer;
	}
