	tick() {
		//this.stats.begin();
		this.compositorRender.update();
		this.compositorRender.render(this.renderer, this.scene, this.renderCamera);
		//this.stats.end();
		this.objectManager.forEach(item => {
			if (item && item.update) {
				item.update();
			}
		});
		if (this.camera.update) {
			this.camera.update();
		}
		this.CSSRender.render(this.scene, this.renderCamera);
		this.update();
		//this.renderer.render(this.scene, this.renderCamera);
		//this.compositorRender.render(this.renderer, this.scene, this.renderCamera);
	}
	update() {
		
	}
	destory() {
		this.domElement.removeEventListener('click', this.handleClick);
		this.renderer.setAnimationLoop(null);
	}
